import React, { useState } from 'react';
import './App.css';
import './index.css';

import About from './Components/About';
import Navbar from './Components/Navbar';
import TextForm from './Components/TextForm';

// npm start

function App() {
  const [mode, setMode] = useState('light');

  const toggleMode = () => {
    if(mode === 'light'){
      setMode('dark');
      document.body.style.backgroundColor = '#212121';  
    }else{
      setMode('light');
      document.body.style.backgroundColor = '#FEFEFE';  
    }
  }

  return (
    <>
    <Navbar title="TextUtils" aboutText="About Us" mode={mode} toggleMode={toggleMode} />

    <div className='container my-2' id="text-cont" style={{backgroundColor: mode==='light'?'blanchedalmond':'#121212', color: mode==='light'?'black':'white', border: mode==='light'?'1px solid black':'1px solid blanchedalmond'}}>
      <TextForm heading='Convert Case' mode={mode} />
    </div>

    <div className="container my-4" style={{border: "1px solid black", borderRadius: "5px" }}>
      <About/>
    </div>

    </>
  );
}

export default App;