import React, {useState} from 'react'
import '../index.css';
import clipboard from "./clipboard.svg";

export default function TextForm(props) {
  
  const handleUpClick = ()=>{
    console.log("Uppercase was clicked");
    let newText = text.toUpperCase();
    setText(newText);
  }

  const handleLoClick = ()=>{
    console.log("Lowercase was clicked");
    let newText = text.toLowerCase();
    setText(newText);
  }

  const handleCopy = ()=>{
    var text = document.getElementById("mybox");
    text.select();
    navigator.clipboard.writeText(text.value);
  }

  const handleExtraSpaces = ()=>{
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
  }

  const handleOnChange = (event)=>{
    console.log("On Change");
    setText(event.target.value);
  }
  
  const [count, setCount] = useState(0);
  const [text, setText] = useState("");
  return (
    <>
    <div className="container">
      <h1>{props.heading}</h1>
      <div className="form-group">
        <textarea className="form-control" value={text} placeholder="Enter text here" id="mybox" onChange={handleOnChange} rows="5" style={{backgroundColor: props.mode==='light'?'white':'#121212', color: props.mode==='light'?'black':'white'}}></textarea>
        <div className="container my-2 pl-0">
          <button className="btn btn-primary btn-sm mr-2" onClick={handleUpClick}>UPPERCASE</button>
          <button className="btn btn-primary btn-sm mr-2" onClick={handleLoClick}>lowercase</button>
          <button className="btn btn-primary btn-sm mr-2" onClick={handleExtraSpaces}>Remove Extra Spaces</button>
          <button className="btn btn-warning btn-sm mr-2" onClick={()=> setText("")}>Clear Text</button>
          <button className="btn btn-success btn-sm mr-2" onClick={handleCopy}><img src={clipboard} alt='clipboard'/>Copy</button>
        </div>
        <h4>Your text summary: </h4>
        <p>{text.split(" ").length} words | {text.length} characters</p>
      </div>
    </div>
    <div className="container">
      <h3>Click Counter</h3>
      <p>You clicked {count} times.</p>
      <button className="btn btn-primary btn-sm mb-2" onClick={()=> setCount(count + 1)} style={{marginRight: 20}}>Click Me</button>
      <button className="btn btn-primary btn-sm mb-2" onClick={()=> setCount(0)}>Reset Counter</button>
    </div>
    </>
  )
}
